html {
  font-size: 16px; }

body {
  background-color: #FFF;
  font-family: "Roboto", sans-serif; }

.d-none {
  opacity: 0 !important;
  visibility: hidden !important; }

.loader {
  background: #191919;
  position: absolute;
  z-index: 100;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh; }
  .loader .loader-flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 100%; }
  .loader .loader-inner {
    background: url(../img/loading.png) no-repeat 0 0 transparent;
    background-size: 100%;
    display: block;
    width: 65px;
    height: 65px;
    margin: 0; }

.sg-glitch_list {
  position: absolute;
  overflow: hidden;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

.sg-glitch_item {
  background-color: transparent;
  background-blend-mode: normal;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: right center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translateZ(0);
  background-size: cover; }

.sg-glitch_item:nth-child(n+2) {
  opacity: 0;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite; }

.sg-glitch_item:nth-child(2) {
  background-color: transparent;
  background-blend-mode: normal;
  -webkit-animation-name: glitch-anim-1;
  animation-name: glitch-anim-1; }

.sg-glitch_item:nth-child(3) {
  background-color: transparent;
  background-blend-mode: normal;
  -webkit-animation-name: glitch-anim-2;
  animation-name: glitch-anim-2; }

.sg-glitch_item:nth-child(4) {
  background-color: transparent;
  background-blend-mode: overlay;
  -webkit-animation-name: glitch-anim-3;
  animation-name: glitch-anim-3; }

.sg-glitch_item:nth-child(5) {
  background-color: transparent;
  background-blend-mode: overlay;
  -webkit-animation-name: glitch-anim-flash;
  animation-name: glitch-anim-flash; }

